.rate-stars-form {
  display: flex;
  flex-direction: column;
  gap: 25px;
  padding-top: 5px;

  h3,
  h5 {
    padding-bottom: 0.5rem;
    color: #62646a;
  }

  h3{
      font-weight: 500;
      font-size: 18px;
  }

  h5{
      font-weight: 400;
  }

  button{
    cursor: pointer;
    width: 50%;
    color: white;
    font-weight: 700;
    background-color: $clr1;
    border: none;
    padding:10px;
    font-size:18px;
    margin-bottom: 30px;
    transition: .3s ;

    &:hover{
      background-color: #19a463;
    }
  }

  .text-area{
      width: 100%;
      textarea{
        display: block;
        resize: none;
        padding: 5px 15px;
        line-height: 1.5;
        box-sizing: border-box;
        width: 100%;
        font-size: 16px;
        color: #606266;
        background-color: #fff;
        border: 1px solid #dcdfe6;
        border-radius: 4px;
        font-family: work-sans;
        min-height:110px;
        outline: none;
        transition: .3s border;

        &:focus{
            border: 1px solid $clr1;
        }
      }
  }
}
