.profile-main-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  min-height: 75vh;
  padding-block: 32px;
  background-color: #fff;
  @include for-wide-header-layout {
      background-color: #cecece;
  }

  .gig-list {
      padding-top: 0;
      flex-grow: 2;
      grid-auto-rows: 400px;
      // @include for-wide-header-layout {
      //     margin-inline-start: 20px;
      // }
      .gig-preview {
          background-color: white;
      }
  }
  .profile-details-container {
      max-width: 100%;
      flex-grow: 1;
  }
  @include for-wide-header-layout {
      flex-direction: row;
      .profile-details-container {
          max-width: 425px;
          flex-grow: 1;
      }
  }
}
.profile-details-container .user-details {
  font-family: Arial, Helvetica, sans-serif;
  padding-block: 32px;
  margin-bottom: 20px;
  padding-inline: 0;
  background-color: #fff;
  @include for-wide-header-layout {
      padding-inline: 32px;
      border: 1px solid $clr6;
  }
  .user-stats-wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      border-bottom: 1px solid $clr6;
      padding-bottom: 30px;
  }
  .online-status {
      position: absolute;
      right: 0;
      color: $clr3;
      font-size: 0.9rem;
      border-radius: 2rem;
      border: 1px solid $clr3;
      padding: 2px 5px;
      font-family: Arial, Helvetica, sans-serif;
      &::before {
          content: "â€¢";
          margin-inline: 4px;
      }
  }
  .online-status.online {
      color: $clr1;
      border: 1px solid $clr1;
  }
  .user-img {
      height: 150px;
      width: 150px;
      font-size: 48px;
      margin-bottom: 30px;
  }
  .fullname {
      font-size: rem(20px);
      color: $clr2;
      font-family: roboto-bold;
  }
}

.profile-details-container .user-info-wrapper {
  padding-top: 30px;
  svg {
      margin-inline-end: 5px;
      height: 20px;
  }
  .origin-wrapper {
      display: flex;
      justify-content: space-between;
      div {
          display: flex;
          align-items: flex-end;
      }
      div:nth-child(2) {
          text-transform: capitalize;
          font-weight: 600;
      }
      margin-bottom: 5px;
  }
  .member-since {
      display: flex;
      justify-content: space-between;
      div {
          display: flex;
          align-items: flex-end;
      }
      div:nth-child(2) {
          font-weight: 600;
      }
  }
}
.profile-details-container .seller-details-container {
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  flex-direction: column;
  gap: 30px;
  background-color: #fff;

  @include for-wide-header-layout {
      padding: 30px;
      border: 1px solid $clr6;
  }

  h2 {
      margin-top: 0;
      color: $clr2;
      font-size: 1.1rem;
  }
  .description-wrapper {
      border-bottom: 1px solid $clr6;
      p {
          line-height: 1.4rem;
          margin: 0;
          margin-bottom: 30px;
      }
  }
  .skills-wrapper {
      ul {
          text-transform: capitalize;
          color: $clr2;
          margin-top: 0px;
          display: flex;
          flex-wrap: wrap;
          gap: 10px;
      }
      .skill {
          border: 1px solid $clr6;
          border-radius: 2em;
          padding: 10px 13px;
          transition: 0.2s;
          &:hover {
              background-color: darken($clr4, 1.4);
          }
      }
  }
  .skill {
      text-transform: capitalize;
  }
}